import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Board from '@/views/board'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
    import news from '@/views/modules/news/list'
    import systemintro from '@/views/modules/systemintro/list'
    import gupiaoleixing from '@/views/modules/gupiaoleixing/list'
    import yonghu from '@/views/modules/yonghu/list'
    import historyinfo from '@/views/modules/historyinfo/list'
    import chat from '@/views/modules/chat/list'
    import gupiaoxinxi from '@/views/modules/gupiaoxinxi/list'
    import topten from '@/views/modules/topten/list'
    import gupiaojiaoyi from '@/views/modules/gupiaojiaoyi/list'
    import config from '@/views/modules/config/list'
    import newstype from '@/views/modules/newstype/list'


//2.   
export const routes = [{
    path: '/',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center', affix: true}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    }
      ,{
	path: '/news',
        name: '',
        component: news
      }
      ,{
	path: '/systemintro',
        name: '',
        component: systemintro
      }
      ,{
	path: '/gupiaoleixing',
        name: '',
        component: gupiaoleixing
      }
      ,{
	path: '/yonghu',
        name: '',
        component: yonghu
      }
      ,{
	path: '/historyinfo',
        name: '',
        component: historyinfo
      }
      ,{
	path: '/chat',
        name: '',
        component: chat
      }
      ,{
	path: '/gupiaoxinxi',
        name: '',
        component: gupiaoxinxi
      }
      ,{
	path: '/topten',
        name: '',
        component: topten
      }
      ,{
	path: '/gupiaojiaoyi',
        name: '',
        component: gupiaojiaoyi
      }
      ,{
	path: '/config',
        name: '',
        component: config
      }
      ,{
	path: '/newstype',
        name: '',
        component: newstype
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/board',
    name: 'board',
    component: Board,
    meta: {icon:'', title:'board'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
export default router;
