<script>
import { 
	isNumber,
} from "@/utils/validate";
export default {
	data() {
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		return {
			id: '',
			type: '',
			
			
			ro:{
				date : false,
				capitalinflow : false,
				balance : false,
				netbuying : false,
				buybalance : false,
				sellbalance : false,
				gainer : false,
				lzgztf : false,
				exponent : false,
				pricelimit : false,
			},
			
			
			ruleForm: {
				date: '',
				capitalinflow: '',
				balance: '',
				netbuying: '',
				buybalance: '',
				sellbalance: '',
				gainer: '',
				lzgztf: '',
				exponent: '',
				pricelimit: '',
			},
		

			
			rules: {
				date: [
				],
				capitalinflow: [
				],
				balance: [
				],
				netbuying: [
				],
				buybalance: [
				],
				sellbalance: [
				],
				gainer: [
				],
				lzgztf: [
				],
				exponent: [
					{ validator: validateNumber, trigger: 'blur' },
				],
				pricelimit: [
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
    components: {
    },
	created() {
	},
	methods: {
		
		// 
		download(file){
			window.open(`${file}`)
		},
		// 
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='date'){
							this.ruleForm.date = obj[o];
							this.ro.date = true;
							continue;
						}
						if(o=='capitalinflow'){
							this.ruleForm.capitalinflow = obj[o];
							this.ro.capitalinflow = true;
							continue;
						}
						if(o=='balance'){
							this.ruleForm.balance = obj[o];
							this.ro.balance = true;
							continue;
						}
						if(o=='netbuying'){
							this.ruleForm.netbuying = obj[o];
							this.ro.netbuying = true;
							continue;
						}
						if(o=='buybalance'){
							this.ruleForm.buybalance = obj[o];
							this.ro.buybalance = true;
							continue;
						}
						if(o=='sellbalance'){
							this.ruleForm.sellbalance = obj[o];
							this.ro.sellbalance = true;
							continue;
						}
						if(o=='gainer'){
							this.ruleForm.gainer = obj[o];
							this.ro.gainer = true;
							continue;
						}
						if(o=='lzgztf'){
							this.ruleForm.lzgztf = obj[o];
							this.ro.lzgztf = true;
							continue;
						}
						if(o=='exponent'){
							this.ruleForm.exponent = obj[o];
							this.ro.exponent = true;
							continue;
						}
						if(o=='pricelimit'){
							this.ruleForm.pricelimit = obj[o];
							this.ro.pricelimit = true;
							continue;
						}
				}
			}
			
		},
    // 

    info(id) {
      this.$http({
        url: `historyinfo/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //
        let reg=new RegExp('../../../upload','g')//g
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 
    onSubmit() {
var objcross = this.$storage.getObj('crossObj');
      //
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
		this.$refs["ruleForm"].validate(valid => {
			if (valid) {
				if(crossrefid && crossuserid) {
					this.ruleForm.crossuserid = crossuserid;
					this.ruleForm.crossrefid = crossrefid;
					let params = { 
						page: 1, 
						limit: 10, 
						crossuserid:this.ruleForm.crossuserid,
						crossrefid:this.ruleForm.crossrefid,
					} 
				this.$http({ 
					url: "historyinfo/page", 
					method: "get", 
					params: params 
				}).then(({ 
					data 
				}) => { 
					if (data && data.code === 0) { 
						if(data.data.total>=crossoptnum) {
							this.$message.error(this.$storage.get('tips'));
							return false;
						} else {
							this.$http({
								url: `historyinfo/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.historyinfoCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});

						}
					} else { 
				} 
			});
		} else {
			this.$http({
				url: `historyinfo/${!this.ruleForm.id ? "save" : "update"}`,
				method: "post",
			   data: this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.parent.showFlag = true;
							this.parent.addOrUpdateFlag = false;
							this.parent.historyinfoCrossAddOrUpdateFlag = false;
							this.parent.search();
							this.parent.contentStyleChange();
						}
					});
				} else {
					this.$message.error(data.msg);
			   }
			});
		 }
         }
		});
    },
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.historyinfoCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
  }
};
</script>
